.ds-form-label,
.ds-form-group label {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  display: inline-block;
  font-family: "3dslight";
  font-size: 1.0625rem;
  color: rgba(0, 0, 0, 0.35);
  border: 0 none;
}

.ds-form {
  display: block;
  margin: 10px 0;
}

.ds-form-fieldset {
  border: 0;
  padding: 0;
  margin: 0;
}

.ds-form-fieldset-content {
  border: solid 1px rgba(0, 0, 0, 0.12);
  border-radius: 25px;
  background: #ffffff;
  margin: 10px 0;
}

.ds-form-group {
  border-top: solid 1px rgba(0, 0, 0, 0.12);
  padding: 17px 24px;
  position: relative;
}

.ds-form-group input[type="text"]:focus,
.ds-form-group input[type="text"]:invalid,
.ds-form-group select {
  box-shadow: none;
  outline: none;
}

.ds-form-group select,
.ds-form-group input[type="text"] {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  display: block;
  width: 100%;
  font-family: "3dslight";
  font-size: 1.0625rem;
  color: rgba(0, 0, 0, 0.57);
  background-color: #ffffff;
  border: 0 none;
  padding: 0;
  margin: 0;
}

.ds-form-group.ds-no-border {
  border: 0;
  background: none;
}

.ds-form-group.ds-form-group-first,
.ds-form-group:first-child {
  border-top: 0 none;
}

.ds-form-group .ds-form-group {
  padding: 0;
  border: 0;
}

.ds-form-group .ds-form-group-element .ds-form-checkbox,
.ds-form-group .ds-form-group-element .ds-form-radio {
  margin-top: 16px;
}

.ds-form-checkbox-type-2 label {
  display: block;
  position: relative;
  padding-left: 30px;
}

.ds-form-checkbox-type-2 label:after,
.ds-form-checkbox-type-2 label:before {
  content: " ";
  cursor: pointer;
  position: absolute;
  margin: 0px;
  padding: 0px;
}

.ds-form-checkbox-type-2 label:after {
  left: 0;
  width: 17px;
  height: 17px;
  border-radius: 2px;
  border: 1px solid rgba(0, 0, 0, 0.12);
  background: transparent;
}

.ds-form-checkbox-type-2 label:before {
  opacity: 0;
  width: 9px;
  height: 5px;
  top: 4px;
  left: 4px;
  border: 2px solid #ffffff;
  border-top: none;
  border-right: none;
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
  z-index: 10;
}

.ds-form-checkbox-type-2 input[type="checkbox"] {
  display: none;
}

.ds-form-checkbox-type-2 input[type="checkbox"]:checked + label::after {
  background: #239DE8;
  -webkit-transition: all 0.5s ease;
  -moz-transition: all 0.5s ease;
  transition: all 0.5s ease;
}

.ds-form-checkbox-type-2 input[type="checkbox"]:checked + label::before {
  opacity: 1;
  -webkit-transition: all 0.5s ease;
  -moz-transition: all 0.5s ease;
  transition: all 0.5s ease;
}

.ds-form-checkbox:last-child {
  margin-bottom: 0;
}

.ds-form-checkbox input[type="checkbox"] {
  display: none;
  opacity: 0;
}

.ds-form-checkbox label {
  display: block;
  padding-right: 50px;
  position: relative;
}

.ds-form-checkbox label:before,
.ds-form-checkbox label:after {
  content: "";
  display: inline-block;
  cursor: pointer;
  position: absolute;
  margin: 0px;
  padding: 0px;
}

.ds-form-checkbox label:before {
  -webkit-transition: all 0.2s ease;
  -moz-transition: all 0.2s ease;
  transition: all 0.2s ease;
  width: 20px;
  height: 20px;
  background: #ffffff;
  border-radius: 20px;
  top: 2px;
  right: 2px;
  z-index: 10;
}

.ds-form-checkbox label:after {
  -webkit-transition: all 0.5s ease;
  -moz-transition: all 0.5s ease;
  transition: all 0.5s ease;
  width: 40px;
  height: 24px;
  background: rgba(0, 0, 0, 0.35);
  border-radius: 20px;
  top: 0px;
  right: 0px;
  z-index: 9;
}

.ds-form-checkbox input[type="checkbox"]:checked + label::before {
  right: 18px;
}

.ds-form-checkbox input[type="checkbox"]:checked + label::after {
  background: #239DE8;
}

.ds-form-radio {
  display: inline-block;
  padding-right: 40px;
}

.ds-form-radio input[type="radio"] {
  display: none;
  opacity: 0;
}

.ds-form-radio label {
  position: relative;
  padding-left: 34px;
}

.ds-form-radio label:after,
.ds-form-radio label:before {
  content: "";
  display: inline-block;
  cursor: pointer;
  position: absolute;
  margin: 0px;
  padding: 0px;
}

.ds-form-radio label:after {
  left: 0;
  top: 0;
  border: 1px solid rgba(0, 0, 0, 0.12);
  border-radius: 100%;
  width: 24px;
  height: 24px;
}

.ds-form-radio label:before {
  left: 5px;
  top: 5px;
  width: 16px;
  height: 16px;
  background: none;
  border-radius: 100%;
}

.ds-form-radio input[type="radio"]:checked + label::before {
  -webkit-transition: all 0.5s ease;
  -moz-transition: all 0.5s ease;
  transition: all 0.5s ease;
  background: #239DE8;
}

