.ds-form-error {
  position: absolute;
  z-index: 20;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  display: block;
  width: calc(100% + 2px);
  padding: 16px 24px;
  background: #e57373;
  border: 1px solid #e57373;
  color: #ffffff;
  font-size: 14px;
  line-height: 14px;
