}

.ds-tabs-header:after {
  content: ' ';
  position: absolute;
  bottom: -16px;
  left: 0;
  right: 0;
  width: 100%;
  height: 5px;
  border-radius: 25px;
  background: rgba(255, 255, 255, 0.22);
