.ds-tabs-header.step-0-active:before,
.ds-tabs-header.step-1-active:before {
  content: ' ';
  position: absolute;
  bottom: -16px;
  width: 50%;
  height: 5px;
  border-radius: 25px;
  background: #ffffff;
  left: 0;
  -webkit-transition: 0.6s ease-in-out all;
  -moz-transition: 0.6s ease-in-out all;
  transition: 0.6s ease-in-out all;
}

.ds-tabs-header.step-0-active li.active,
.ds-tabs-header.step-1-active li.active {
  color: #ffffff;
  -webkit-transition: 1s ease all;
  -moz-transition: 1s ease all;
  transition: 1s ease all;
}

.ds-tabs-header.step-0-active li.active a,
.ds-tabs-header.step-1-active li.active a {
  cursor: default;
  color: #ffffff;
  -webkit-transition: 1s ease all;
  -moz-transition: 1s ease all;
  transition: 1s ease all;
}

.ds-tabs-header.step-0-active:before {
  left: 0;
}

.ds-tabs-header.step-1-active:before {
  left: 50%;
}

.ds-tabs-header li {
  list-style: none;
  text-align: center;
  display: inline-block;
